import { Component } from '@angular/core';
import { EventbusService } from '../core/eventbus.service';
import { UtilityService } from '../core/utility.service';
@Component({
  selector: 'app-tabs',
  templateUrl: 'tabs.page.html',
  styleUrls: ['tabs.page.scss'],
})
export class TabsPage {
  user_role: any = JSON.parse(localStorage.getItem('userObj')).role;
  user_image: any = JSON.parse(localStorage.getItem('userObj')).image;
  constructor(
    private eventBus: EventbusService,
    public utility: UtilityService
  ) {
    this.eventBus.getObservable().subscribe((res) => {
      console.log('here', this.user_image);
    });
  }
}
